import React from "react"
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { Checkbox, Button, formatMs } from '@material-ui/core/';
import BackWithLottie from "../Components/Backdrop/backdeopWithLottie"
import {makeOramgeMoneyPaymentUrl}  from "../Controllers/ApiCall"
import { useSnackbar } from "notistack";

const useStyles = makeStyles((theme) => ({
    tableHead: {
        background: theme.palette.primary.main,
        color: "white",
        height: "30px",
    },

    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
    },
}));


export default function MakeOrder(props) {


    return (
        <div style={{
            backgroundColor: "white",
            display: "flex", alignItems: "center", justifyContent: "center", height: "100%", width: "100%"
        }}>
            <ProductList {...props} />
        </div>
    );
}

function ProductList(props) {
    const classes = useStyles();

    const {enqueueSnackbar} = useSnackbar();
 
  
    const handleClickVariant = (msg,variant) => {
        // variant could be success, error, warning, info, or default
        enqueueSnackbar(msg, { variant });
      };

    const [open, setOpen] = React.useState(false);
    const handleClose = () => {
        setOpen(false);
    };
    const handleToggle = async () => {
        setOpen(!open);
        let total = getTotal();
        if(total==0){
            handleClickVariant('Please select some products','error');
        }else{
        let id=props?.AuthState.User.id;
           let url = await makeOramgeMoneyPaymentUrl(id, total);
        if (url != null) {
            window.open(url);
        }}
        handleClose();
    };

    function getTotal  () { 
        var value=0;
        product.forEach(val=>{if(val.isInCart)value+=val.total});
        return value;
     }

    const [product, SetProducts] = React.useState([{
        name: "pen",
        price: 1000,
        unit: 1,
        total: 1000,
        isInCart: false,
    },
    {
        name: "book",
        price: 5000,
        unit: 1,
        total: 5000,
        isInCart: false,
    }, {
        name: "pencil",
        price: 3000,
        unit: 1,
        total: 3000,
        isInCart: false,
    }, {
        name: "bag",
        price: 5000,
        unit: 1,
        total: 5000,
        isInCart: false,
    }, {
        name: "ruler",
        price: 4000,
        unit: 1,
        total: 4000,
        isInCart: false,
    },
    ]);



    function checkBoxChecked(value, ind) {
        var prod = product;
        var obj = prod[ind];
        obj.isInCart = value.target.checked;
        prod[ind] = obj;
        SetProducts([...prod]);
    }

    function valueChange(value, ind) {
        value.preventDefault();
        var prod = product;
        var obj = prod[ind];
        obj.total = (value.target.value * obj.price);
        obj.unit = value.target.value;
        prod[ind] = obj;
        console.log(prod)
        SetProducts([...prod]);
    }

    return (<>
        <table style={{ width: "80%", height: "200px" }}>
            <tr className={classes.tableHead}>
                {
                    ["Product", "Price", "Unit", "Total", "Cart"].map((val, ind) =>
                        <th key={ind}>{val}</th>
                    )
                }
            </tr>
            {product.map((value, ind) =>
                <tr key={ind} style={{ textAlign: "center" }}>
                    <td style={{ textAlign: "start" }}>{value.name.toUpperCase()}</td>
                    <td>{value.price}</td>
                    <td><input
                        onChange={(value) => valueChange(value, ind)}
                        style={{ width: "70px", height: "30px" }}
                        defaultValue={value.unit}
                        type="number"
                        min={1}
                    /></td>
                    <td> {`le ${value.total}`}</td>
                    <td><Checkbox
                        onChange={(newVal) => checkBoxChecked(newVal, ind)}
                        style={{ width: "50px" }}
                    /></td>
                </tr>)
            }
            <tr>
                <td>
                    <Button
                        onClick={handleToggle}
                        variant="contained" color="secondary">
                        Pay with OrangeMoney
                   </Button>
                </td>

            </tr>
        </table>
        <BackWithLottie handleClose={handleClose} open={open} />
    </>
    );

}
