import React from 'react';
import PropTypes from 'prop-types';
import CssBaseline from '@material-ui/core/CssBaseline';
import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import { ListItemText, Paper, fade } from '@material-ui/core';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import {
    BrowserRouter, Route, Switch,
    Redirect, NavLink
} from "react-router-dom";
import transitions from '@material-ui/core/styles/transitions';
const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    drawer: {
        [theme.breakpoints.up('sm')]: {
            width: drawerWidth,
            flexShrink: 0,
        },
    },
    appBar: {
        [theme.breakpoints.up('sm')]: {
            width: `calc(100% - ${drawerWidth}px)`,
            marginLeft: drawerWidth,
        },
    },
    menuButton: {
        marginRight: theme.spacing(2),
        [theme.breakpoints.up('sm')]: {
            display: 'none',
        },
    },
    // necessary for content to be below app bar
    toolbar: theme.mixins.toolbar,
    drawerPaper: {
        width: drawerWidth,
    },
    content: {
        flexGrow: 1,
        padding: theme.spacing(3),
    }, divider: {
        marginRight: "20px",
        marginLeft: "20px",
        backgroundColor: theme.palette.secondary.main,

    }, logo: {
        display: "flex",
        justifyContent: "center",
        alignItems: "center",

    }, selected: {
        backgroundColor: fade(theme.palette.primary.main, 0.5),
        marginBottom: "7px",
        marginLeft: "7px",
        marginRight: "7px",
        color: "white",
        transition: "all 0.2s ease-out",
    }, notSelected: {
        backgroundColor: 'transparent',
        marginBottom: "0px",
        transition: "all 0.2s ease-in",

    }
}));

function ResponsiveDrawer(props) {

    const { window } = props;
    const classes = useStyles();
    const theme = useTheme();

    const [] = React.useState();

    const drawer = (
        <div>
            <div className={`${classes.toolbar} ${classes.logo}`} >
                <p style={{
                    fontSize: "20px",
                }}> Mobile Money Test </p> &nbsp;
                {/* <sub style={{ fontSize: "12px", color: "green" }}>admin</sub> */}
            </div>
            <Divider className={classes.divider} />

            <List >
                {
                    props.selectedRoute.DrawerRoutes.map((val, index) => (
                        <NavLink
                            style={{ textDecoration: "none" }}
                            to={val.Layout + val.Route}
                            onClick={() => props.setRouteName(val.Name)} >
                            <Paper elevation={props.selectedName === val.Name ? 3 : 0}
                                className={props.selectedName === val.Name ?
                                    classes.selected : classes.notSelected}
                            >
                                <ListItem button key={index} >
                                    <ListItemIcon>{<val.Icon color="primary"  />}</ListItemIcon>
                                    <ListItemText primary={val.Name} />
                                </ListItem>
                            </Paper>
                        </NavLink>
                    ))}
            </List>
        </div>
    );

    const container = window !== undefined ? () => window().document.body : undefined;

    return (
        <div className={classes.root}>
            <CssBaseline />

            <nav className={classes.drawer} aria-label="mailbox folders">
                {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
                <Hidden smUp implementation="css">
                    <Drawer
                        container={container}
                        variant="temporary"
                        anchor={theme.direction === 'rtl' ? 'right' : 'left'}
                        open={props.mobileOpen}
                        onClose={() => props.handleDrawerToggle()}
                        classes={{
                            paper: classes.drawerPaper,
                        }}
                        ModalProps={{
                            keepMounted: true, // Better open performance on mobile.
                        }}
                    >
                        {drawer}
                    </Drawer>
                </Hidden>
                <Hidden xsDown implementation="css">
                    <Drawer
                        classes={{
                            paper: classes.drawerPaper,
                        }}
                        variant="permanent"
                        open
                    >
                        {drawer}
                    </Drawer>
                </Hidden>
            </nav>
            {
                props.children
            }
        </div>
    );
}

ResponsiveDrawer.propTypes = {
    /**
     * Injected by the documentation to work in an iframe.
     * You won't need it on your project.
     */
    window: PropTypes.func,
};

export default ResponsiveDrawer;


