import {
    ShoppingCart,  
    History,
 
} from "@material-ui/icons";

  
 import MakeOrder from "../Views/MakeOrder";
 import ConfirmPay from "../Views/ConfirmPay"
 import ViewRecords from "../Views/ViewRecords"


import { SvgManagement} from "../Assets/SVGIcons/SvgIcons"


const homeRoute = [
    {
        Route: "/MakeOrder"
        , Name: "Make Order",
        Component: MakeOrder,
        Icon: ShoppingCart,
        Layout: "/admin/home",
    },
    {
        Route: "/Records"
        , Name: "Records",
        Component: ViewRecords
        , Icon: History,
        Layout: "/admin/home",
    },
    {
        Route: "/Confirm Payment"
        , Name: "Confirm Payment",
        Component: ConfirmPay
        , Icon: SvgManagement,
        Layout: "/admin/home",
    }

]
export {
    homeRoute 
};