import React from "react"
import { makeStyles } from '@material-ui/core/styles';

import { useLocation, Link } from 'react-router-dom'



const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
    root: {


        [theme.breakpoints.up('sm')]: {
            width: `calc(100% - ${drawerWidth}px)`,
            marginLeft: drawerWidth,
            display: "flex",
            position: "fixed",
            alignItems: "end",
            justifyContent: "center",
            backgroundColor: "white",
        },

    }
    ,
    toolbar: theme.mixins.toolbar,

    grow: {
        [theme.breakpoints.up('sm')]: {
            flexGrow: 1,
        },

    }

}));

export default function NavBar(props) {
    const classes = useStyles();

    return (
        <>
            <div className={classes.toolbar} />
            <div className={classes.root}>
                <div className={classes.grow} />
                <NavLinks routeState={props.routeState} />

            </div>
        </>
    );
}



const NaveUseStyles = makeStyles((theme) => ({

    rootUl: {
            listStyle: "none",

        // [theme.breakpoints.down('sm')]: {
        //     listStyle: "none",
        //     textAlign: "center",
        //     position:"absolute",
        //     width:"100%"
        // },

        [theme.breakpoints.up('sm')]: {
            
            display: "flex",
            flexWrap: "no-wrap"
            ,
            paddingRight: theme.spacing(5),
            color: theme.palette.primary.main,
        },

    },
    rootlI: {

        textDecoration: "none",
        backgroundImage: `linear-gradient(${theme.palette.primary.main}, ${theme.palette.primary.main}),linear-gradient(transparent, transparent),linear-gradient(${theme.palette.secondary.main}, ${theme.palette.secondary.main})`,
        backgroundSize: "20px 3px, 100% 3px, 0 3px",
        backgroundPosition: `calc(20px * -1) 100%, 100% 100%, 0 100%`,
        backgroundRepeat: "no-repeat",
        transition: "background-size 500ms linear, background-position 500ms linear",
        marginLeft: theme.spacing(2),
        marginRight: theme.spacing(2),
        cursor: "pointer",
        "&:hover": {
            backgroundSize: "20px 3px, 0 3px, 100% 3px",
            backgroundPosition: `calc(100% + 20px) 100%, 100% 100%, 0 100%`,
        },


    }

}));


function NavLinks(props) {
    const classes = NaveUseStyles();
    const location = useLocation();



    return (
        <div>
            <ul className={classes.rootUl}>
                {
                    props.routeState.routes.map((val, i) =>
                        <li key={val.path + i}
                            // onClick={() => {
                            //     props.routeState.setSelectedRoute(val.Name)
                            // }}
                        ><Link className={classes.rootlI} to={val.path}>{
                            val.Name}</Link></li>)
                }
            </ul>
        </div>
    );
}