import React from "react"
import {AuthContext} from "./contexts"


class AuthContextProvider extends React.Component{

constructor(){
super();
    this.state={
        isUsersLoggedIn:false,
        User:{}
    }

}
componentDidMount(){
   var storedData= window.localStorage.getItem('state');
if(storedData!=null && this.state.isUsersLoggedIn==false){
  this.setState(prev=>({...storedData}))
}
}

setLoginState=(state)=>{
this.setState(prev=>({...state}))
window.localStorage.setItem('state', this.state);
}
  render(){

    return(
        <AuthContext.Provider
        value={{
        ...this.state,
        setloginState:this.setLoginState,
        }     
        }
        >
            {
             this.props.children
            }
        </AuthContext.Provider>

    );
  }


}
const AuthContextConsumer=AuthContext.Consumer;
export {AuthContextProvider,AuthContextConsumer}
