import React from "react"
import { NavContext } from "./contexts"
import { withRouter } from 'react-router-dom';

import {
    homeRoute,
    TransactionRoute,
    MerchantsRoute,
    FSPsRoute,
    DevicesRoute,
    CustomersRoute,
    AnalysisRoute,
} from "../Routes/Routes"

class NavigationContext extends React.PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            routes: [
                {
                    Name: "Home",
                    DrawerRoutes: homeRoute,
                    path: "/admin/home",


                }  
            ],

            selectedName: " ",

        }

    }

 
    setSelectedRoute = (selectedName) => {
        this.setState({selectedName });
    }

    render() {

        return (
            <NavContext.Provider
                value={
                    {
                        ...this.state,
                        setSelectedRoute: this.setSelectedRoute
                    }
                }
            >
                {
                    this.props.children
                }
            </NavContext.Provider>

        );
    }

}
const NavContextConsumer = NavContext.Consumer;

const NavContextProvider = withRouter(NavigationContext);

export { NavContextProvider, NavContextConsumer }
