
import React from "react";
import ReactDOM from "react-dom";
import { createBrowserHistory } from "history";
import {
  BrowserRouter, Route, Switch,
  Redirect, Router
} from "react-router-dom";
import AdminWorkSpace from "./layout/AdminWorkSpace"

import SignIn from "./layout/SignIn"
import reportWebVitals from './reportWebVitals';
import { AuthContextConsumer, AuthContextProvider } from "./StateManagement/AuthContext"
import { NavContextProvider, NavContextConsumer } from "./StateManagement/NavContext"

import RouteWithSubRoutes from "./Routes/RouteWithSubRoute"
import { SnackbarProvider } from 'notistack';
import Success from "./layout/SuccesPay";


ReactDOM.render(
  <SnackbarProvider maxSnack={3}>

  <AuthContextProvider>
    <AuthContextConsumer>
      {
        value =>
          <BrowserRouter 
          basename={process.env.PUBLIC_URL}
          >
            <Switch>
              <Route exact path="/merchant/signIn"
                render={props => (
                 <SignIn {...props} AuthState={value} />
                )}
              />
              <Route exact path="/merchant/paymentMade"
                render={props => (
                  <Success {...props} />
                 )}
              />
              <Redirect exact from="/" to="/merchant/signIn" />
              <NavContextProvider>
                <NavContextConsumer>
                  
                  {
                    routeStates => routeStates.routes.map((routes, index) =>
                      <Route
                        path={routes.path}
                        render={
                          props => (
                            <AdminWorkSpace key={index} {...props} value={routes} AuthState={value} routeState={routeStates} />
                          )}
                      />
                    )
                  }
                </NavContextConsumer>
              </NavContextProvider>
            </Switch>
          </BrowserRouter>}
    </AuthContextConsumer>
  </AuthContextProvider>
  </SnackbarProvider>,
  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
