import React from "react";
import { NavContextProvider, NavContextConsumer } from "../StateManagement/NavContext"
import {
    Route, Switch,
    Redirect
} from "react-router-dom";
import StatusBar from "../Components/statusBar/StatusBar"
import ResponsiveDrawer from "../Components/drawer/Drawer"
import NavBar from "../Components/navbar/NavMenu"
import { useLocation } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles';
const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({

    toolbar: theme.mixins.toolbar,

    content: {
        flexGrow: 1,
        padding: theme.spacing(3),
    },
}));



export default function AdminWorkSpace({ AuthState,value, routeState, ...props }) {
    const location = useLocation();
    const classes = useStyles();
    const [mobileOpen, setMobileOpen] = React.useState(false);



    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen);
    };


    let drawerFirstRoute = value.DrawerRoutes[0];


    React.useEffect(() => {
        routeState.setSelectedRoute(drawerFirstRoute.Name)
    }, [drawerFirstRoute])

    const SwitchRoutes = (
        <Switch>

            {
                value.DrawerRoutes.map((prop, key) => {
                    return (
                        <Route
                            path={`${prop.Layout}${prop.Route}`}
                            render={(renderProp) => <prop.Component 
                                AuthState={AuthState}
                                {...renderProp} name={prop.Name} />}
                            key={prop.Layout}
                        />
                    );
                })}
            <Redirect from={location.pathname} to={drawerFirstRoute.Layout + drawerFirstRoute.Route} />
        </Switch>
    );

    return (
        <>
            <StatusBar handleDrawerToggle={handleDrawerToggle}
                selectedName={routeState.selectedName} />
            {/* <NavBar routeState={routeState} /> */}
            <ResponsiveDrawer
                selectedName={routeState.selectedName}
                handleDrawerToggle={handleDrawerToggle}
                mobileOpen={mobileOpen}
                setRouteName={routeState.setSelectedRoute}
                selectedRoute={value}
                {...props}
            >
                <main className={classes.content}>
                    <div className={classes.toolbar} />
                    
                        {SwitchRoutes}
                    
                </main>
            </ResponsiveDrawer>
        </>
    )

}