import axios from "axios"
 
async  function  makeOramgeMoneyPaymentUrl(usersId,Amount)  {
  const domainName =  window.location.hostname;

  let body={  
      "successUrl":`${domainName}/merchant/paymentMade`,
      "returnUrl":`${domainName}/admin/home/MakeOrder`,
      "cancleUrl":`${domainName}/merchant/paymentMade`,
      "totalAmount": Amount,
      "UserId":usersId
  }

const apiUrl = 'http://kemmanuel-001-site1.ftempurl.com/api/MobileMoney/makeOrangeMoneyPayment';

return await axios.post(apiUrl,body).then((res) => res.data).then((data)=>data["payment_url"]);
}


async  function  fakeLogin(usersName,password)  {
     const apiUrl = 'http://kemmanuel-001-site1.ftempurl.com/api/Customers/login';
 
 return await axios.get(apiUrl, {
  params: {
    fName: usersName,
    password:password,
  }
}).then((res) => res.data);
}


async  function getUsersFullData(id)  {
 
 
  const apiUrl = 'http://kemmanuel-001-site1.ftempurl.com/api/Customers/getById';
return await axios.get(apiUrl, {
params: {
  id: id
}}).then((res) => res.data);

}

async  function  checkStatus(clientID,orderID,notif_token,status,txnid)  {
const apiUrl = 'https://localhost:5001/api/MobileMoney/onPaymentMade';
let body={  
  "status": status,
  "notif_token": notif_token,
  "txnid": txnid
}

return await axios.get(apiUrl, body, {
params: {
  clientID,
  orderID
}
}).then((res) => res.data);
}

