import Avatar from '@material-ui/core/Avatar';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import Container from '@material-ui/core/Container';
import CssBaseline from '@material-ui/core/CssBaseline';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import IconButton from '@material-ui/core/IconButton';
import InputAdornment from '@material-ui/core/InputAdornment';
import Link from '@material-ui/core/Link';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import React, { useRef } from 'react';
import CircularProgress from '@material-ui/core/CircularProgress'
import { Bounce } from "react-awesome-reveal";
import {
  fakeLogin
} from "../Controllers/ApiCall";

import BackdropwithLottie  from  "../Components/Backdrop/backdeopWithLottie"

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright  '}
      <Link color="inherit" href="https://smart.tech/">
        Smart Tech
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  paper: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',

    borderTopRightRadius: 29,
    borderTopLeftRadius: 29,

    borderBottomRightRadius: 29,
    borderBottomLeftRadius: 29,
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    // marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),

    paddingRight: theme.spacing(3),
    paddingLeft: theme.spacing(3),

  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  bottom: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "blue",
    height: "50px",
    width: "100%",
    borderTopRightRadius: 0,
    borderTopLeftRadius: 0,
    borderBottomRightRadius: 29,
    borderBottomLeftRadius: 29,
    color: "white",
    cursor: "pointer",
  },
  top: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "blue",
    height: "50px",
    width: "100%",
    borderTopRightRadius: 29,
    borderTopLeftRadius: 29,
    color: "white",
  }
  , status: {
    marginTop: theme.spacing(4),
    marginBottom: theme.spacing(.5),

    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  }, loading: {
    justifyContent: "space-evenly",
    alignItems: "center",
    display: "flex",
    width: "200px",
   }
}));



export default function SignIn(props) {
  const classes = useStyles();

  const [open, setOpen] = React.useState(false);
  const handleClose = () => {
    setOpen(false);
  };
  const handleToggle = () => {
    setOpen(!open);
  };
const passwordRef=useRef();
const nameRef=useRef();

  const onSigninSuccess = () => {
     props.history.push("/admin/home");
  }

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const handleClickShowPassword = () => {
    setValues({ ...values, showPassword: !values.showPassword });
  };

  const [values, setValues] = React.useState({
    showPassword: false,
  });

  const [verified, verifying] = React.useState(false
  );

  const  handleVerifyClick = async () => {

    handleToggle(); 
    verifying(!verified);
let usersname=nameRef.current.value;
let password=passwordRef.current.value;
  if(usersname!=null&&password!=null){
 let user= await fakeLogin(usersname.trim(),password.trim());
 if(user?.fName!=null){
  props.AuthState.setloginState({isUsersLoggedIn:true,
  User:{...user}})
  handleClose();
  onSigninSuccess();
  }
  console.log("=============================")
  console.log(user)
 }else{
  console.log(`NAME: ${usersname} PASSWORD:${password}`);
  console.log("invalid Name And Password");
  handleClose();
 }
 

  };

  React.useEffect((revProps) => {
  }, []);

  return (
    <>
     <Container component="main" maxWidth="xs">

      <CssBaseline />
      <div className={classes.status}>
        {verified && <Bounce><p>satus corrent or fail  </p></Bounce>}
        {!verified && <Bounce  ><div className={classes.loading}>
          <p>{"Loading Please Wait "} </p> <CircularProgress size={18} /></div></Bounce>}
      </div>

      <Card className={classes.paper}>
        <div className={classes.top}>
          <Avatar className={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar>
        </div>

        <Typography component="h1" variant="h5">
          Sign in
        </Typography>

        <form className={classes.form} noValidate>

          <FormControl
            fullWidth
            margin="normal"

          >
            <FormHelperText id="outlined-weight-helper-text">Users Name</FormHelperText>
            <OutlinedInput
              id="users-name"
              type={'text'}
              inputRef={nameRef}

            />
          </FormControl>

          <FormControl
            fullWidth
            margin="normal"
          >
            <FormHelperText id="outlined-weight-helper-text">Secrete ID</FormHelperText>
            <OutlinedInput
              id="users-secrete"
              type={'text'}
            />
          </FormControl>
          {/* <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="Secret ID"
            label="Secret ID"
            type="password"
            id="password"
            autoComplete="current-password"
          /> */}

          <FormControl
            fullWidth
            margin="normal"

          >
            <FormHelperText id="outlined-weight-helper-text">Password</FormHelperText>
            <OutlinedInput
            inputRef={passwordRef}
              id="standard-adornment-password"
              type={values.showPassword ? 'text' : 'password'}
              value={values.password}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={handleClickShowPassword}
                    onMouseDown={handleMouseDownPassword}
                  >
                    {values.showPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              }
            />
          </FormControl>

        </form>
        <Button
// onClick={onSigninSuccess}
          onClick={handleVerifyClick}
          variant="contained"
          color="primary"
          className={classes.bottom}>
          Done
        </Button>

      </Card>

      <Box mt={6}>
        <Copyright />
      </Box>
    </Container>
   <BackdropwithLottie handleClose={handleClose} open={open}/>
    </>
  );
}