import React from "react"
import { makeStyles, useTheme, fade } from '@material-ui/core/styles';
import { IconButton } from '@material-ui/core/';
import { Delete } from '@material-ui/icons';

import BackWithLottie from "../Components/Backdrop/backdeopWithLottie"
import { getUsersFullData } from "../Controllers/ApiCall"
const useStyles = makeStyles((theme) => ({
    tableHead: {
        background: theme.palette.primary.main,
        color: "white",
        height: "30px",
        fontWeight:"150px",
    }, tableBody1: {
        height: "30px",
        padding: "5px",
        color: "white",
        backgroundColor: theme.palette.primary.light,
    }, tableBody2: {
        height: "30px",
        padding: "5px",
        color: "white",
        backgroundColor: fade(theme.palette.primary.main, .2),

    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
    },
}));


export default function ViewRecords(props) {

    return (
        <div style={{
            backgroundColor: "white",
            // flexDirection:"column",
            // display: "flex", alignItems:"center", justifyContent:"space-around" 
        }}>
            <React.Fragment>
                <div style={{
                    backgroundColor: "white",
                    display: "flex", alignItems: "center", justifyContent: "center",
                    fontWeight: "bold"
                    , fontSize: "5rm"
                }}>
                    <h1>Orders</h1>
                </div>
                <ProductList {...props} type={"order"} />
            </React.Fragment>
            <React.Fragment>
                <div style={{
                    backgroundColor: "white",
                    display: "flex", alignItems: "center", justifyContent: "center",
                    fontWeight: "bold"
                    , fontSize: "5rm"
                }}>
                    <h1>Payments</h1>
                </div>
                <ProductList {...props} type={"transaction"} />
            </React.Fragment>
        </div>
    );
}

function ProductList(props) {
    const [Orders, setOrders] = React.useState(null);
    const [Payment, setPayment] = React.useState(null);

    React.useEffect(async () => {
        if (Payment==null || Orders.length==null) {
            let data = await getUsersFullData(props?.AuthState.User.id);
if(data!=null||data==undefined){
             
             if(data.orders!=null){
let orderObj=[...data?.orders]
let paymentsObj=[...data?.payments]

orderObj = orderObj.forEach((obj)=>{
    delete obj.payments
    return obj;
});

    paymentsObj = paymentsObj.forEach((obj)=>{
        delete obj.orderNavigation
        delete obj.deliveries
        return obj;
    });
             setOrders([...data?.orders]);
            setPayment([...data?.payments])
             }
}
        }
    }, [])

    const classes = useStyles();

    const [open, setOpen] = React.useState(false);
    const handleClose = () => {
        setOpen(false);
    };
    const handleToggle = () => {
        setOpen(!open);
    };


    const [product, SetProducts] = React.useState([{
        name: "pen",
        price: 1000,
        unit: 1,
        total: 1000,
        isInCart: false,
    },
    {
        name: "book",
        price: 5000,
        unit: 1,
        total: 5000,
        isInCart: false,
    }, {
        name: "pencil",
        price: 3000,
        unit: 1,
        total: 3000,
        isInCart: false,
    }, {
        name: "bag",
        price: 5000,
        unit: 1,
        total: 5000,
        isInCart: false,
    }, {
        name: "ruler",
        price: 4000,
        unit: 1,
        total: 4000,
        isInCart: false,
    },
    ]);



    function checkBoxChecked(value, ind) {
        var prod = product;
        var obj = prod[ind];
        obj.isInCart = value.target.checked;
        prod[ind] = obj;
        SetProducts([...prod]);
    }

    function valueChange(value, ind) {
        value.preventDefault();
        var prod = product;
        var obj = prod[ind];
        obj.total = (value.target.value * obj.price);
        obj.unit = value.target.value;
        prod[ind] = obj;
        console.log(prod)
        SetProducts([...prod]);
    }

    switch(props.type){
        case "order":
            return (<>
                {Orders && <table style={{ height: "200px" }}>
                    <tr className={classes.tableHead}>
                        {
                            [...Object.keys(Orders[0]), "Delete"].map((val, ind) =>
                              <th style={{ width: "100px",padding:"5px" }} key={ind} >{val.toString().toUpperCase()}</th>
                            )
                        }
                    </tr>
           
                    {Orders.map((value, ind) =>
                     <tr key={ind} style={{ textAlign: "center",height:"20px" }} className={
                            ind % 2 == 0 ?
                                classes.tableBody1 : classes.tableBody2}>
                            <td style={{ textAlign: "start" , width: "100px"}}>{value.id}</td>
                            <td>  {value.user} </td>
        
                            <td style={{ width: "100px"}}>{value.totalAmount}</td>
                            <td style={{ width: "100px"}}>  {value.date} </td>
                             <td style={{ maxWidth: "100px", wordWrap:"break-word" }}>  {value.payToken} </td>
                            <td style={{ maxWidth: "100px",wordWrap:"break-word"}}>  {value.notifToken} </td>
                            <td style={{ width: "100px"}}><IconButton aria-label="delete"
                                Click={() => { }}
                                style={{ width: "50px",color:"white", }}
        
                            >
                                <Delete />
                            </IconButton>
        
                            </td>
        
                        </tr>)
                    }
        
                </table>}
                <BackWithLottie handleClose={handleClose} open={open} />
            </>
            );        
            break;
            case "transaction":
                return (<>
                    {Payment && <table style={{ height: "200px" }}>
                        <tr className={classes.tableHead}>
                            {
                             Object.keys(Payment[0]).map((val, ind) =>
                                  <th style={{ width: "100px",padding:"5px" }} key={ind} >{val.toString().toUpperCase()}</th>
                                )
                            }
                        </tr>
                      
                        {Payment.map((value, ind) =>
                         <tr key={ind} style={{ textAlign: "center",height:"20px" }} className={
                                ind % 2 == 0 ?
                                    classes.tableBody1 : classes.tableBody2}>
                                <td style={{ textAlign: "start" , width: "100px"}}>{value.id}</td>
                                <td>  {value.user} </td>
            
                                <td style={{ width: "100px"}}>{value.order}</td>
                                <td style={{ width: "100px"}}>  {value.date} </td>
                                 <td style={{ maxWidth: "100px", wordWrap:"break-word" }}>  {value.success?"true":"false"} </td>
                                <td style={{ maxWidth: "100px",wordWrap:"break-word"}}>  {value.status} </td>
                                <td style={{ maxWidth: "100px" ,wordWrap:"break-word"}}>
                                {value.payToken}
                                </td>
            
                            </tr>)
                        }
            
                    </table>}
                    <BackWithLottie handleClose={handleClose} open={open} />
                </>
                );            
                break;

                default:
                    return (<>
                        {Orders && <table style={{ height: "200px" }}>
                            <tr className={classes.tableHead}>
                                {
                                    [...Object.keys(Orders[0]), "Delete"].map((val, ind) =>
                                      <th style={{ width: "100px",padding:"5px" }} key={ind} >{val.toString().toUpperCase()}</th>
                                    )
                                }
                            </tr>
                   
                            {Orders.map((value, ind) =>
                             <tr key={ind} style={{ textAlign: "center",height:"20px" }} className={
                                    ind % 2 == 0 ?
                                        classes.tableBody1 : classes.tableBody2}>
                                    <td style={{ textAlign: "start" , width: "100px"}}>{value.id}</td>
                                    <td>  {value.user} </td>
                
                                    <td style={{ width: "100px"}}>{value.totalAmount}</td>
                                    <td style={{ width: "100px"}}>  {value.date} </td>
                                     <td style={{ maxWidth: "100px", wordWrap:"break-word" }}>  {value.payToken} </td>
                                    <td style={{ maxWidth: "100px",wordWrap:"break-word"}}>  {value.notifToken} </td>
                                    <td style={{ width: "100px"}}><IconButton aria-label="delete"
                                        Click={() => { }}
                                        style={{ width: "50px",color:"white", }}
                
                                    >
                                        <Delete />
                                    </IconButton>
                
                                    </td>
                
                                </tr>)
                            }
                
                        </table>}
                        <BackWithLottie handleClose={handleClose} open={open} />
                    </>
                    );                

                    break;
    }

 
}
